 .footer {
     width: 100%;

     padding-bottom: 28px;
     padding-top: 28px;

     background-color: getColor(footer-bcg);
     font-weight: 400;
     @include lh(16px, 14px, 14px);
     color: getColor(footer-text-color);
     text-align: center;

     @media screen and (min-width: getSize(tablet)) {
         @include lh(19px, 16px, 16px);
         padding-bottom: 30px;
         padding-top: 30px;
     }

     @media screen and (min-width: getSize(desktop)) {
         padding-bottom: 28px;
     }
 }

 .footer__container {
     @media screen and (min-width: getSize(tablet)) {
         display: flex;
         justify-content: center;
         align-items: center;
     }
 }

 .footer__info {
     margin-bottom: 10px;

     @media screen and (min-width: getSize(tablet)) {
         margin-bottom: 0;

         &:not(:last-child) {
             margin-right: 4px;
         }
     }
 }

 .footer__icon {
     margin-right: 10px;
     display: inline-block;
 }

 .footer__link {
     margin: 0;
     text-decoration: underline;
     color: getColor(footer-text-color);
 }

 .footer__icon {
     fill: getColor(accent-text-color);
     animation: Scale 0.7s ease infinite alternate;
 }

 .footer__text {
     margin-right: 10px;
 }

 .footer__btn {
     padding: 0;
     border: none;
     font-weight: 400;
     @include lh(16px, 14px, 14px);
     cursor: pointer;
     color: getColor(footer-text-color);
     background-color: transparent;
     border-bottom: 1px solid getColor(footer-text-color);
     transition: text-shadow getProperty(time) getProperty(cubic);


     @media screen and (min-width: getSize(tablet)) {
         @include lh(19px, 16px, 16px);
     }

     &:hover,
     &:focus {
         text-shadow: 0 0 10px #faf7f7;
     }
 }