.overlay.is-hidden .footer__wrapper {
    transform: translate(-50%, -50%) scale(0.4);
    opacity: 0;
}

.overlay.footer__wrapper {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
}

.footer__wrapper {
    width: 600px;
    max-width: 95vw;
    border-radius: 20px;

}

.foother-modal__btn {
    width: 30px;
    height: 30px;
    cursor: pointer;

    background-color: getColor(white-text-color);
    border: 1px solid getColor(footer-text-color);
    border-radius: 50%;

    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
    position: absolute;
    top: 10px;
    right: 10px;

    transition: border-color getProperty(time) getProperty(cubic);

    &:hover,
    &:focus {
        border: 1px solid getColor(current-page);
    }

}