 .container {

     max-width: 480px;
     padding-left: 20px;
     padding-right: 20px;
     margin: 0 auto;

     @media screen and (min-width: getSize(tablet)) {
         max-width: 768px;
         padding-left: 32px;
         padding-right: 32px;
     }

     @media screen and (min-width: getSize(desktop)) {
         max-width: 1280px;
     }
 }